<template>
      <el-row class="tac" width="120px">
      <el-col :span="8">
        <el-menu mode="vertical" default-active="1" :unique-opened="true" class="el-menu-vertical-demo" :router="true">
        <el-submenu index="1" title="">
            <template slot="title"></template>
            <el-menu-item :route="articleList" index="5"></el-menu-item>
            <el-menu-item :route="articleUpdate" index="6"></el-menu-item>
        </el-submenu>
        <el-submenu index="2" title="">
            <template slot="title"></template>
            <el-menu-item :route="albumList" index="7"></el-menu-item>
            <el-menu-item :route="albumAdd" index="8"></el-menu-item>
        </el-submenu>
        <el-submenu index="3" title="">
            <template slot="title"></template>
            <el-menu-item :route="audioList" index="9"></el-menu-item>
            <el-menu-item :route="audioUpdate" index="10"></el-menu-item>
        </el-submenu>
        <el-submenu index="4" title="">
            <template slot="title"></template>
            <el-menu-item :route="videoList" index="11"></el-menu-item>
            <el-menu-item :route="videoUpdate" index="12"></el-menu-item>
        </el-submenu>
        <el-submenu index="15" title="">
            <template slot="title"></template>
            <el-menu-item :route="userList" index="13"></el-menu-item>
            <el-menu-item :route="me" index="14"></el-menu-item>
            <el-menu-item :route="addUser" index="16"></el-menu-item>
        </el-submenu>
        </el-menu>
    </el-col>
    <el-col :span="19">
        <router-view></router-view>
    </el-col>
  </el-row>
</template>
<script>
  import axios from 'axios'
  export default {
    created(){
        this.token = window.localStorage.getItem('token');
        axios.defaults.params = {
            token: this.token
        }
    },
    data() {
        return {
            username: '',
            token: null,
            articleList: {
                path: '/article/list'
            },
            articleUpdate: {
                path: '/article/update/:-1'
            },
            albumList: {
                path: '/album/list'
            },
            albumAdd: {
                path: '/album/add/:-1'
            },
            audioList: {
                path: '/audio/list'
            },
            audioUpdate: {
                path: '/audio/update/:-1'
            },
            videoList: {
                path: '/video/list'
            },
            videoUpdate: {
                path: '/video/update/:-1'
            },
            userList: {
                path: '/user/list'
            },
            me: {
                path: '/user/me'
            },
            addUser: {
                path: '/user/add'
            }
        }
    },
    methods: {
      
    }
  }

</script>
<style>
    .el-row {
        margin-bottom: 0;
    }
    #app,.tac, .tac .el-col-8, .tac .el-col-8 ul {
        height: 100%;
    }
    .el-col-8 {
        width: 230px !important;
    }
</style>



